@charset "UTF-8";
.nav__list-logo {
  color: rgba(51, 39, 0, 0.8509803922);
  font-size: 1.5rem;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  background-color: rgb(255, 251, 240);
  color: rgba(51, 39, 0, 0.8980392157);
}

img {
  width: 100%;
  height: 100%;
}

.btn {
  background-color: #FFCC54;
  border: none;
}

.btn__sm {
  box-shadow: 0px 0.5px 3px rgba(51, 39, 0, 0.5);
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
}

.btn__md {
  box-shadow: 0px 0.5px 3px rgba(51, 39, 0, 0.5);
  border-radius: 0.5rem;
  padding: 1rem 2rem;
}

.download-btn {
  display: inline-block;
  width: 19rem;
}

.search-btn {
  display: inline-block;
  background-color: rgba(120, 129, 128, 0.7490196078);
  width: 5rem;
  padding: 2em;
  border-radius: 0 1rem 1rem 0;
}

.nav {
  position: absolute;
  text-align: center;
  top: 100%;
  right: 0;
  background-color: rgba(255, 251, 240, 0.9);
  width: 100%;
  transform: scale(1, 0);
  transform-origin: top;
  transition: transform 400ms ease-in-out;
}

.nav__logo {
  text-align: left;
  margin-left: 1em;
}

.nav__lists {
  list-style-type: none;
}

.nav__list {
  margin-bottom: 1em;
  margin-right: 1em;
}

.nav__list__link {
  text-decoration: none;
  font-size: 1.2rem;
  text-transform: uppercase;
  color: rgba(138, 106, 0, 0.8980392157);
  opacity: 0;
}

.nav__toggle:checked ~ .nav {
  transform: scale(1, 1);
}

.nav__toggle:checked ~ .nav__list__link {
  opacity: 1;
}

.nav__toggle {
  display: none;
}

.nav__toggle__label {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 1em;
  height: 100%;
  display: flex;
  align-items: center;
}

@media (min-width: 500px) {
  .nav__toggle__label {
    display: none;
  }
  header {
    display: flex;
  }
  nav {
    all: unset;
  }
  .nav__list__link {
    opacity: 1;
  }
  .nav__list {
    display: block;
  }
  .nav {
    background-color: red;
    display: block;
  }
}
.footer {
  color: #FFFBF0;
  background-color: rgba(132, 139, 137, 0.7490196078);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.footer__icon {
  display: inline-block;
  width: 40%;
}

.footer__details {
  flex-basis: 60%;
}

.footer__contact li {
  list-style-type: none;
  flex-basis: 33%;
}

.header {
  text-align: center;
  width: 100%;
  position: relative;
}

.section-1 {
  background: linear-gradient(to bottom, rgba(255, 251, 240, 0.6), rgba(255, 251, 240, 0.9)), url(/app/images/picture-1.png) no-repeat center center;
  background-size: cover;
  padding-top: 1em;
}

.article-1 {
  text-align: center;
  font-size: 1.2rem;
  padding: 0.5em;
}

.article-1 h1 {
  font-size: 3rem;
}

.article-1__highlight-1 {
  color: #DE6101;
}

.article-1__highlight-2 {
  color: #8A6A00;
}

.card-1 {
  background-color: rgba(255, 208, 84, 0.8509803922);
  border-radius: 2em;
}

.card-2 {
  background-color: rgba(14, 2, 92, 0.7490196078);
  border-radius: 2em;
}

.card-3 {
  background-color: rgba(12, 112, 3, 0.7490196078);
  border-radius: 2em;
}

.card__text {
  padding: 2.5em;
  text-align: left;
  border-radius: 0 0 2em 2em;
}

.card__ratings {
  display: flex;
  align-items: center;
  gap: 1em;
}

.card__ratings__icon {
  display: inline-block;
  width: 30%;
  height: 10%;
}

.card__ratings__text {
  margin-top: 1em;
}

.card__icons {
  display: flex;
  align-items: center;
  gap: 3em;
}

@media (min-width: 500px) {
  .cards {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.section-2 {
  text-align: center;
}

.search-box {
  display: flex;
  padding: 1em;
  justify-content: center;
  background-color: rgba(196, 196, 196, 0.6);
}

.search-box input {
  width: 70%;
  background-color: #FFFBF0;
  border: none;
}

.search-box select {
  border-radius: 1rem 0 0 1rem;
  background-color: #FFFBF0;
  border: none;
  border-right: 2px solid rgba(51, 39, 0, 0.1490196078);
}

.section-3__bg {
  background: url("/app/images/Frame 91.png") no-repeat center center;
  background-size: cover;
  width: 40%;
  margin: auto;
}

.section-3__text {
  text-align: center;
  padding: 4em;
}

@media (min-width: 500px) {
  .section-3 {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
  }
}
.section-4__bg {
  background: url("/app/images/Frame 92.png") no-repeat center center;
  background-size: contain;
  width: 40%;
  margin: auto;
}

.section-4__text {
  text-align: center;
  padding: 4em;
}

@media (min-width: 500px) {
  .section-4 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.section-5 {
  text-align: center;
}

.section-5__box {
  width: 50%;
  text-align: center;
  padding: 2em;
  border-radius: 1.8rem;
  margin: 2em auto;
}

.section-5__box-1 {
  background-color: rgba(254, 227, 207, 0.5019607843);
}

.section-5__box-2 {
  background-color: rgba(79, 146, 182, 0.9490196078);
}

.section-5__box-3 {
  background-color: rgba(254, 227, 207, 0.5019607843);
}

.section-5__icon {
  margin: auto;
  display: block;
  width: 40%;
}

@media (min-width: 500px) {
  .section-5 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.section-6 {
  margin: auto;
  text-align: center;
  font-size: 1rem;
  background-color: rgba(163, 142, 129, 0.1019607843);
}

.section-6__bg {
  width: 40%;
  margin: auto;
}

.section-6__quote::before {
  content: "“";
  font-weight: 500;
  font-size: 3.5rem;
}

.section-6__quote::after {
  content: "”";
  font-weight: 500;
  font-size: 3.5rem;
}

@media (min-width: 500px) {
  .section-6 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .section-6__bg {
    width: 20%;
  }
}
.section-7 {
  display: flex;
  flex-wrap: wrap;
}

.section-7__lists {
  text-align: left;
  flex-basis: 50%;
}

.section-7__name {
  margin-left: 3.5em;
}

.section-7__list {
  list-style-type: none;
}

.text-left {
  text-align: right;
  margin-right: 7.5em;
}

@media (min-width: 500px) {
  .section-7__lists {
    flex-basis: 25%;
  }
}