.section-6{
  margin: auto;
  text-align: center;
  font-size: 1rem;
  background-color: #A38E811A;
}
.section-6__bg{
  width: 40%;
  margin: auto;
}
.section-6__quote::before{
   content :'“';
   font-weight: 500;
  font-size: 3.5rem;

}
.section-6__quote::after{
  content :'”';
  font-weight: 500;
  font-size: 3.5rem;
}
